@use "global" as *;

a {
    text-decoration: none;
}

body h1 {
    text-align: center;
}

footer {
    text-align: center;
}

#formWrap {
    width: 700px;
    margin: 0 auto;
    color: #555;
    line-height: 120%;
    font-size: 90%;
}

table.formTable {
    width: 100%;
    margin: 0 auto;
    border-collapse: collapse;
}

table.formTable td,
table.formTable th {
    border: 1px solid #ccc;
    padding: 10px;
}

table.formTable th {
    width: 30%;
    font-weight: normal;
    background: #efefef;
    text-align: left;
}

/*　簡易版レスポンシブ用CSS（必要最低限のみとしています。ブレークポイントも含め自由に設定下さい）　*/
@media screen and (max-width:572px) {
    #formWrap {
        width: 95%;
        margin: 0 auto;
    }

    table.formTable th,
    table.formTable td {
        width: auto;
        display: block;
    }

    table.formTable th {
        margin-top: 5px;
        border-bottom: 0;
    }

    form input[type="text"],
    form textarea {
        width: 80%;
        padding: 5px;
        font-size: 110%;
        display: block;
    }

    form input[type="submit"],
    form input[type="reset"],
    form input[type="button"] {
        display: block;
        width: 100%;
        height: 40px;
    }
}